"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { CheckCircle2, BookOpen, Trash2, Edit2, Loader2 } from "lucide-react"
import { getReadingData } from "@/lib/api-client"
import { ThemeToggle } from "@/components/theme-provider"

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: any) {
  const [readings, setReadings] = useState<any[]>([])
  const [tempBookNames, setTempBookNames] = useState<Record<number, string>>({})
  const [isUpdating, setIsUpdating] = useState(false)

  const now = new Date()
  const todayStr = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}-${String(now.getDate()).padStart(2, '0')}`

  async function loadData() {
    if (!userEmail) return
    try {
      const response: any = await getReadingData(userEmail, year)
      const finalData = response?.data ? response.data : response
      setReadings(Array.isArray(finalData) ? finalData : [])
    } catch (err) { console.error(err) }
  }

  useEffect(() => { loadData() }, [userEmail, monthIndex, year])

  async function handleAction(day: number, action: string, bookName?: string) {
    if (isUpdating || !userEmail) return
    const name = bookName || tempBookNames[day]
    if (!name && (action === "START_READING" || action === "EDIT_READING")) return
    
    setIsUpdating(true)
    const dateFormatted = `${year}-${String(monthIndex + 1).padStart(2, '0')}-${String(day).padStart(2, '0')}T12:00:00Z`
    
    try {
      await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
          action, 
          email: userEmail, 
          bookName: name, 
          startDate: dateFormatted, 
          year, 
          month: monthIndex + 1 
        })
      })
      setTempBookNames(p => ({ ...p, [day]: "" }))
      await loadData()
    } finally { 
      setIsUpdating(false) 
    }
  }

  return (
    <div className="relative space-y-8 p-4 max-w-[1400px] mx-auto min-h-screen">
      {/* O BOTÃO DARK MODE FICA AQUI NO CANTO */}
      <ThemeToggle />

      <div className="text-center">
        <h2 className="text-5xl font-serif font-bold text-foreground capitalize tracking-tighter">
          {month}
        </h2>
        <div className="mt-3">
          <span className="px-4 py-1.5 rounded-full bg-primary text-primary-foreground text-[11px] font-black uppercase tracking-[0.2em] shadow-md border border-border">
            {readings.filter(r => r.status === 'lido').length} Concluídos
          </span>
        </div>
      </div>

      <div className="grid grid-cols-2 gap-4 md:grid-cols-7">
        {Array.from({ length: days || 31 }, (_, i) => i + 1).map((day) => {
          const currentDayStr = `${year}-${String(monthIndex + 1).padStart(2, '0')}-${String(day).padStart(2, '0')}`
          const isToday = currentDayStr === todayStr
          const isFuture = currentDayStr > todayStr
          
          const dayReadings = readings.filter(r => {
            const startStr = r.start_date?.split('T')[0]
            const endStr = r.end_date?.split('T')[0]
            return currentDayStr >= startStr && (!endStr || currentDayStr <= endStr)
          })

          return (
            <div 
              key={day} 
              className={`min-h-[220px] rounded-2xl border p-4 flex flex-col transition-all duration-300 shadow-sm 
                ${isToday ? 'bg-primary/30 border-primary ring-1 ring-primary shadow-lg scale-[1.02] z-10' : 'bg-card border-border'} 
                ${isFuture ? 'opacity-30 grayscale pointer-events-none' : 'hover:border-primary'}`}
            >
              <div className="flex justify-between items-center mb-4">
                <span className={`text-[12px] font-black ${isToday ? 'text-foreground' : 'text-muted-foreground'}`}>
                  {day}
                </span>
                {isUpdating && <Loader2 size={10} className="animate-spin text-foreground opacity-40" />}
              </div>
              
              <div className="flex-1 space-y-3">
                {dayReadings.map((r, idx) => {
                  const isLendo = r.status === "lendo";
                  return (
                    <div key={idx} className={`group relative p-3 rounded-xl border transition-all ${isLendo ? 'bg-background border-border shadow-sm' : 'bg-muted/40 border-transparent opacity-70'}`}>
                      
                      <div className="absolute -top-2 -right-1 flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity z-20">
                        <button 
                          onClick={() => {
                            const newName = prompt("Novo nome do livro:", r.book_name);
                            if (newName) handleAction(day, "EDIT_READING", newName);
                          }}
                          className="p-1 bg-card border border-border rounded-md text-foreground/60 hover:text-foreground shadow-sm"
                        >
                          <Edit2 size={10} />
                        </button>
                        <button 
                          onClick={() => confirm("Excluir?") && handleAction(day, "DELETE_READING", r.book_name)}
                          className="p-1 bg-card border border-border rounded-md text-red-500 hover:bg-red-50 shadow-sm"
                        >
                          <Trash2 size={10} />
                        </button>
                      </div>

                      <div className="flex items-center gap-2 mb-2">
                        {isLendo ? <BookOpen size={12} className="text-foreground/70" /> : <CheckCircle2 size={12} className="text-muted-foreground" />}
                        <p className={`text-[10px] font-bold uppercase truncate flex-1 ${isLendo ? 'text-foreground' : 'text-muted-foreground'}`}>
                          {r.book_name}
                        </p>
                      </div>
                      
                      {isLendo && (
                        <Button 
                          size="sm" 
                          className="h-7 w-full text-[9px] font-black uppercase rounded-lg bg-primary text-primary-foreground hover:brightness-110 border border-border/50 shadow-sm" 
                          onClick={() => handleAction(day, "FINISH_READING", r.book_name)}
                        >
                          Encerrar
                        </Button>
                      )}
                    </div>
                  )
                })}
              </div>

              {!isFuture && (
                <div className="mt-4 pt-4 border-t border-border/60 space-y-2">
                  <input 
                    className="w-full h-8 text-[10px] bg-transparent border-none focus:ring-0 placeholder:text-muted-foreground/50 font-bold px-1 text-foreground" 
                    placeholder="Livro..." 
                    value={tempBookNames[day] || ""} 
                    onChange={e => setTempBookNames(p => ({ ...p, [day]: e.target.value }))} 
                    onKeyDown={(e) => e.key === 'Enter' && handleAction(day, "START_READING")}
                  />
                  <Button 
                    variant="ghost"
                    className="h-8 w-full text-[9px] font-black uppercase rounded-lg text-foreground/60 hover:bg-primary hover:text-primary-foreground transition-colors" 
                    onClick={() => handleAction(day, "START_READING")}
                  >
                    + Iniciar
                  </Button>
                </div>
              )}
            </div>
          )
        })}
      </div>
    </div>
  )
}